<template>
	<div class="everynew">
		<div class="title">
			<van-icon name="arrow-left" class="arrow" size="20px" @click="onClickLeft()"/>
			<p>每日新品</p>
		</div>
		<div class="goods">
			<ul>
				<li v-for="(val,key) in img"><img :src="val.path" alt="" @click="changeurl(key)">
				<p>{{val.p}}</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {
		Icon
	} from 'vant';

	Vue.use(Icon);
	export default {
		name: 'everynew',
		data() {
			return {
			img:[{path:require('../assets/images/new8.png'),p:"扫地机器人",url:'/goods'},
			{path:require('../assets/images/new1.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new2.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new3.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new4.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new5.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new6.png'),p:"米家智能摄像头",url:'/goods'},
			{path:require('../assets/images/new7.png'),p:"米家智能摄像头",url:'/goods'}]
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			changeurl(key){
				this.$router.push({ path: this.img[key].url, query: { Id: key }})
			}
		}
	}
</script>

<style scoped lang="less">
	.title {
		height: 50px;
		width: 100%;
		position: relative;
		.arrow{
			position: absolute;
			top: 15px;
			left: 15px;
		}
		p{
			text-align: center;
			line-height: 50px;
			color: rgba(56, 56, 56, 1);
				font-size: 15px;
		}
	}
	.goods{
		ul>li{
			height: 200px;
			width: 50%;
			float: left;
			text-align: center;
			img{
				height: 100px;
				width: 100px;
			}
			p{
				text-align: center;
				font-size: 15px;
			}
		}
	}
</style>
