import Vue from 'vue'
import VueRouter from 'vue-router'
import MainPage from '../views/MainPage.vue'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Youpin from '../views/Youpin.vue'
import My from '../views/My.vue'
import Sucess from '../views/Sucess.vue'
import Shop from '../views/Shop.vue'
import Connect from '../views/Connect.vue'
import Baike from '../views/Baike.vue'
import Wifi from '../views/Wifi.vue'
import Cup from '../views/Cup.vue'
import Addlanya from '../views/Addlanya.vue'
import Goods from '../views/Goods.vue'
import Lanya from '../views/Lanya.vue'
import Shezhi from '../views/Shezhi.vue'
import Everynew from '../views/Everynew.vue'
import Intelligence from '../views/Intelligence.vue'
Vue.use(VueRouter)

const routes = [{
		path: '/',
		component: MainPage,
		children: [{
				path: '',
				component: Home,
			},
			{
				path: 'youpin',
				component: Youpin
			},
			{
				path: 'intelligence',
				component: Intelligence
			},
			{
				path: 'my',
				component: My
			}
		]
	},
	{
		path: '/everynew',
		name: Everynew,
		component: Everynew
	},
	{
		path: '/goods',
		name: Goods,
		component: Goods
	},
	{
		path: '/shop',
		name: Shop,
		component: Shop
	},
	{
		path: '/login',
		component: Login
	},
	{
		path: '/baike',
		component: Baike
	},
	{
		path: '/shezhi',
		component: Shezhi
	},
	{
		path: '/connect',
		component: Connect
	},
	{
		path: '/wifi',
		component: Wifi
	},
	{
		path: '/sucess',
		component: Sucess
	},
	{
		path: '/lanya',
		component: Lanya
	},
	{
		path: '/addlanya',
		component: Addlanya
	},
	{
		path: '/cup',
		component: Cup
	},
	// {
	//   path: '/about',
	//   name: 'About',
	//   // route level code-splitting
	//   // this generates a separate chunk (about.[hash].js) for this route
	//   // which is lazy-loaded when the route is visited.
	//   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
	// }
]

const router = new VueRouter({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes
})

export default router
