<template>
	<div class="mainpage">
	<div class="footer">
		<ul>
			<li v-for="(val,key) in li" @click="changebg(key)"><router-link :to="val.address" ><div :class="{isActive:val.color,icon:!val.color}" class="icon1" ><van-icon :name="val.name"  class="icon1" size="20"/>
			<p>{{val.p}}</p></div></router-link></li>
		</ul>
	</div>
	<router-view/>
	
	</div>
</template>

<script>
	import Vue from 'vue'
	import {Icon} from 'vant'
	Vue.use(Icon)
	export default {
	  name: 'Home',
	   data(){
	      return {
	        li:[{name:'wap-home',color:true,p:'米家',address:'/'},{name:'bag',color:false,p:'有品',address:'/youpin'},{name:'medal',color:false,p:'智能',address:'/intelligence'},{name:'manager',color:false,p:'我的',address:'/my'}]
			}
		  },
		  methods: {
		      changebg(index){
				  for(var i = 0;i<this.li.length;i++){
					  this.li[i].color=false;
				  }
				  this.li[index].color=true;
			  }
			  
		    }
	}
</script>

<style scoped lang="less">
	div.mainpage{
				padding-bottom: 60px;
				box-sizing: border-box;
				height: 667px;
				position: relative;
				z-index: 10;
	}

	.footer{
	z-index: 10;
	width: 100%;
	height: 60px;
	position: fixed;
	bottom: 0;
	left: 0;
	background: white;}
	.footer>ul{height: 100%;}
	.footer>ul>li{float: left;width: 25%;height: 70px;}
	.footer>ul>li>a{height: 100%;width: 100%;display: block;}
	.icon1>p{height: 18px;text-align: center;font-size: 12px;}
	.icon1{text-align: center;}
	.icon{color:#dcdcdc}
	.isActive{color: #07BC9B;}
</style>
