<template>
  <div class="home">
    <div class="mi"><img src="../assets/images/logo.png" alt=""></div>
	<p class="region">大陆地区</p>
	<p class="register" @click="toreg">注册</p>
	<form action="" class="reg">
	<p><img src="../assets/images/phone.png" alt=""><input type="text" v-model="number" placeholder="请输入手机号"></p>
	<p><img src="../assets/images/guard.png" alt=""><input type="password" v-model="password" placeholder="请输入验证码"></p>
	<input type="submit" @click="login" value="登录" class="login">
	</form>
	<div class="other-login"><p></p><p>其他方式登录</p><p></p></div>
	<ul class="circle">
		<li><p><img src="../assets/images/weixin.png" alt=""></p>
		<p>微信登录</p></li>
		<li><p><img src="../assets/images/f.png" alt=""></p>
		<p>Facebook</p></li>
		<li><p><img src="../assets/images/lock.svg" alt=""></p>
		<p>密码登录</p></li>
	</ul>
	<div class="reg-div">
		<p>注册</p>
		<div><van-field v-model="number" type="number" label="用户名:" class="input user" placeholder="6-12位数字" />
		<!-- 输入密码 -->
		<van-field v-model="password" type="password" label="密码:" class="input pd" placeholder="6-12位数字字母组合" /></div>
		<button class="login" @click="reg">注册</button>
	</div>
	</div>
</template>

<script>
import Vue from 'vue';
import { Field } from 'vant';

Vue.use(Field);
export default {
  name: 'Login',
   data(){
      return {
        number:'',
		password:''
		}
	  },
	  methods: {
		  login(){
			  if(window.localStorage.getItem('key')==this.number){
				  if(window.localStorage.getItem('pd')==this.password){
					  this.$router.push('/');
				  }
			  }
			
		  },
	      toreg(){
			  var reg = document.getElementsByClassName('reg-div')[0];
			  reg.style.display="block"
		  },
		  reg(){
			  var us=/^\d{6,12}$/;
			  var pa=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$/;
			  if(us.test(this.number)&&pa.test(this.password)){
				var key =  window.localStorage.getItem('key');
				window.localStorage.clear()
				if(!key){
					window.localStorage.setItem('key',this.number);
					window.localStorage.setItem('pd',this.password);
					var reg = document.getElementsByClassName('reg-div')[0];
					reg.style.display="none"
				}
				else{
					alert("注册失败")
				}
			  }
			  else{
				  alert("注册失败")
			  }
			  
		  }
	    }
}
</script>
<style scoped lang='less'>
