.mi{height: 66px;width: 66px;margin: 74px auto 0;}
.mi>img{height: 66px;width: 66px;display: block;}
.region{height: 22px;width: 60px;position: absolute;top: 12px;right: 15px;color: rgba(7, 188, 155, 1);font-size: 15px;line-height: 150%;}
.register{
	height: 22px;width: 60px;position: absolute;top: 12px;right: 50px;color: rgba(7, 188, 155, 1);font-size: 15px;line-height: 150%;
}
.reg-div{
	height: 667px;
	width: 375px;
	position: absolute;
	display: none;
	top: 0;
	left: 0;
	p:nth-child(1){
		text-align: center;
		padding: 10px;
		box-sizing: border-box;
	}
	background: url("../assets/images/home-bg.jpg") no-repeat center/cover;overflow: hidden;
	div{
		.input{
			display: block;
			width: 80%;
			margin: 0 auto;
			margin-top: 15px;
			background: transparent;
			border-bottom: 1px solid #d0c4c4;;
		}
	}
}
.reg{margin-top: 64px;}
.reg>p{position: relative;width: 335px;margin: 0 auto;padding: 5px;box-sizing: border-box;height: 50px;border-bottom:1px solid #d0c4c4;}
.reg>p>input{width: 100%;height: 100%;outline: none;border: none;background: transparent;padding-left: 50px;color:#757575;font-size: 16px;}
.reg>p:nth-child(1)>img{width: 20px;height: 20px;display: block;position: absolute;top: 15px;left: 10px;}
.reg>p:nth-child(2)>img{width: 14px;height: 16px;display: block;position: absolute;top: 15px;left: 10px;}
.login{width: 320px;height:40px;display: block;margin: 23px auto 0;color: rgba(255, 255, 255, 1);background-color: rgba(7, 188, 155, 1);border-radius: 22px;font-size: 18px;line-height: 150%;text-align: center;outline: none;border: none;}
.other-login{width: 320px;height: 21px;position: relative;margin: 196px auto 0;}
.other-login>p:nth-child(2){color: rgba(128, 128, 128, 1);font-size: 14px;line-height: 150%;text-align: center;}
.other-login>p:nth-child(1){border-top: 1px solid #d0c4c4;position: absolute;width: 100px;top: 10px;left: 15px;}
.other-login>p:nth-child(3){border-top: 1px solid #d0c4c4;position: absolute;width: 100px;top: 10px;right: 15px;}
.circle>li{width: 33%;float: left;height: 60px;padding: 10px;box-sizing: border-box;}
.circle>li>p:nth-child(1){height: 36px;width: 36px; color: rgba(80, 80, 80, 1);border-radius: 19px;margin: 0 auto;background: rgba(219, 219, 219, 1);overflow: hidden;}
.circle>li>p:nth-child(2){width: 100%;height: 18px;color: rgba(80, 80, 80, 1);font-size: 12px;line-height: 150%;text-align: center;}
.circle>li>p>img{margin-top: 10px;}
.circle>li:nth-child(1)>p>img{height: 26px;width: 26px;display: block;margin: 5px auto 0;}
.circle>li:nth-child(2)>p>img{height: 20px;width: 10px;display: block;margin: 5px auto 0;}
.circle>li:nth-child(3)>p>img{height: 22px;width: 22px;display: block;margin: 5px auto 0;}
</style>
