<template>
	
	<div class="shop">
		<div class="title">
			<van-icon name="arrow-left" class="arrow" size="20px" @click="onClickLeft()"/>
			<p>购物车</p>
		</div>
	<van-card v-for="(val,key) in p"
	  :num="val[2]"
	  :price="val[1]"
	  :title="val[0]"
	  :thumb="val[3]">
	  <template #footer>
	    <van-button size="mini" @click="reduce(val[4])">-</van-button>
	    <van-button size="mini" @click="add(val[4])">+</van-button>
		<van-button size="mini" class="close" @click="close(val[4])">×</van-button>
	  </template>
	  
	</van-card>
	<div class="nullshop">购物车为空<span @click="goshop">去商店</span></div>
	<div class="fixed">
		<p class="jiesuan"><span class="total">合计：</span>￥{{price}}元</p>
		<van-goods-action>
			<van-goods-action-icon icon="chat-o" text="客服" />
			<van-goods-action-icon icon="cart-o" text="购物车" />
			<van-goods-action-icon icon="shop-o" text="店铺" />
			<van-goods-action-button type="danger" text="结算" @click=""/>
		</van-goods-action>
	</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import { Card } from 'vant';
	
	Vue.use(Card);
	export default{
		data(){
			return{
				p:[],
				price:0
			}
		},
		beforeMount(){
			if(window.localStorage.getItem('1001')){
				this.p.push(window.localStorage.getItem('1001').split(','))
				this.price+=this.p[0][1]*this.p[0][2];
			}
			if(window.localStorage.getItem('1002')){
				this.p.push(window.localStorage.getItem('1002').split(','));
				this.price+=this.p[0][1]*this.p[0][2];
			}
		

		},
		mounted(){
			if(this.p.length==0){
					var null1 = document.getElementsByClassName('nullshop')[0];
					null1.style.display="block";
				}
		},
		methods: {
			goshop(){
				this.$router.push('/everynew')
			},
			onClickLeft() {
				this.$router.go(-1)
			},
			add(key){
				if(key=='1001'){
					var arr = window.localStorage.getItem('1001').split(',')
					arr[2]=parseInt(arr[2])+1;
					if(arr[2]>=10){
						arr[2]=10;
					}
					window.localStorage.setItem('1001',arr);
					var a=0;
					for(var i =0;i<this.p.length;i++){
						if(this.p[i][4]==key){
							a=i;
						}
					}
					this.p.splice(a,1,window.localStorage.getItem('1001').split(','));
				}
				if(key=='1002'){
					var arr = window.localStorage.getItem('1002').split(',')
					arr[2]=parseInt(arr[2])+1;
					if(arr[2]>=10){
						arr[2]=10;
					}
					
					window.localStorage.setItem('1002',arr);
					var a=0;
					for(var i =0;i<this.p.length;i++){
						if(this.p[i][4]==key){
							a=i;
						}
					}
					this.p.splice(a,1,window.localStorage.getItem('1002').split(','))
				}
				this.price=0;
				for(var i=0 ; i<this.p.length;i++){
					this.price+=this.p[i][1]*this.p[i][2];
				}
				
			},
			reduce(key){
				if(key=='1001'){
					var arr = window.localStorage.getItem('1001').split(',')
					arr[2]=parseInt(arr[2])-1;
					if(arr[2]<=1){
						arr[2]=1;
					}
					window.localStorage.setItem('1001',arr);
					var a=0;
					for(var i =0;i<this.p.length;i++){
						if(this.p[i][4]==key){
							a=i;
						}
					}
					this.p.splice(a,1,window.localStorage.getItem('1001').split(','));
				}
				if(key=='1002'){
					var arr = window.localStorage.getItem('1002').split(',')
					arr[2]=parseInt(arr[2])-1;
					if(arr[2]<=1){
						arr[2]=1;
					}
					window.localStorage.setItem('1002',arr);
					var a=0;
					for(var i =0;i<this.p.length;i++){
						if(this.p[i][4]==key){
							a=i;
						}
					}
					this.p.splice(a,1,window.localStorage.getItem('1002').split(','))
					console.log(this.p)
				}
				this.price=0;
				for(var i=0 ; i<this.p.length;i++){
					this.price+=this.p[i][1]*this.p[i][2];
				}
				
			},
			close(key){
				var a=0;
				for(var i =0;i<this.p.length;i++){
					if(this.p[i][4]==key){
						a=i;
					}
				}
				this.p.splice(a,1);
				window.localStorage.removeItem(key);
				this.price=0;
				for(var i=0 ; i<this.p.length;i++){
					this.price+=this.p[i][1]*this.p[i][2];
				}
				if(this.p.length==0){
						var null1 = document.getElementsByClassName('nullshop')[0];
						null1.style.display="block";
					}
			}
			}
	}
</script>

<style scoped lang="less">
	.title {
		height: 50px;
		width: 100%;
		position: relative;
		.arrow{
			position: absolute;
			top: 15px;
			left: 15px;
		}
		p{
			text-align: center;
			line-height: 50px;
			color: rgba(56, 56, 56, 1);
				font-size: 15px;
		}
	}
	.fixed {
		height: 50px;
		z-index: 10;
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
		.jiesuan{
			.total{
				color: rgba(56, 56, 56, 1);
				font-size: 14px;
			}
			padding-right: 30px;
			box-sizing: border-box;
			height: 50px;
			width: 100%;
			color: rgba(212, 48, 48, 1);
			font-size: 14px;
			line-height: 50px;
			text-align: right;
			position: absolute;
			top: -50px;
			left: 0;
		}
	}
	.close{
		position: absolute;
		top: 0;
		right: 10px;
		background: transparent;
		font-size: 15px;
	}
	.nullshop{
		display: none;
		height: 50px;
		width: 150px;
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		margin: auto auto;
		font-size: 20px;
		line-height: 50px;
		span{
			color: limegreen;
			font-size: 15px;
		}
	}
</style>
