<template>
	<div class="wifi">
		<div class="title">
			<van-icon name="arrow-left" class="arrow" size="20px" @click="onClickLeft()" />
			<p>连接工作Wifi</p>
		</div>
		<div class="center">
			<img src="../assets/images/WI-FI.svg" alt="">
		</div>
		<div class="bottom">
			<div class="wifi">
				<van-field v-model="fieldValue" is-link readonly label="Wi-Fi" placeholder="请选择Wi-Fi"
					@click="show = true" />
				<van-popup v-model="show" round position="bottom">
					<van-cascader v-model="cascaderValue" title="请选择Wi-Fi" :options="options" @close="show = false"
						@finish="onFinish" />
				</van-popup>
			</div>
			<div class="password"><van-field type="password" label="密码"/></div>
			<div class="warn"><img src="../assets/images/warn.svg" alt="">不支持5GWi-Fi</div>
			<p @click="success">下一步</p>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {
		Cascader
	} from 'vant';
import { Field } from 'vant';

Vue.use(Field);
	Vue.use(Cascader);
	export default {
		data() {
			return {
				show: false,
				fieldValue: '',
				cascaderValue: '',
				// 选项列表，children 代表子选项，支持多级嵌套
				options: [{
						text: 'TP-LINK770',
						value: '1',
						// children: [{
						// 	text: '杭州市',
						// 	value: '330100'
						// }],
					},
					{
						text: 'TP-LINK880',
						value: '2',
						// children: [{
						// 	text: '南京市',
						// 	value: '320100'
						// }],
					},
					{
						text: 'TP-LINK990',
						value: '3',
						// children: [{
						// 	text: '南京市',
						// 	value: '320100'
						// }],
					},
					{
						text: 'TP-LINK110',
						value: '4',
						// children: [{
						// 	text: '南京市',
						// 	value: '320100'
						// }],
					},
				],
			};
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			success(){
				this.$router.push('/connect')
			},
			// 全部选项选择完毕后，会触发 finish 事件
			onFinish({
				selectedOptions
			}) {
				this.show = false;
				this.fieldValue = selectedOptions.map((option) => option.text).join('/');
			}
		}
	}
</script>

<style scoped lang="less">
	.title {
		height: 50px;
		width: 100%;
		position: relative;

		.arrow {
			position: absolute;
			top: 15px;
			left: 15px;
		}

		p {
			text-align: center;
			line-height: 50px;
			color: rgba(56, 56, 56, 1);
			font-size: 15px;
		}
	}

	.center {
		height: 216px;
		width: 100%;
		background: #E9E9E9;
		overflow: hidden;

		img {
			display: block;
			height: 150px;
			width: 150px;
			margin: 31px auto 35px;
		}
	}

	.bottom {
		height: calc(100vh - 266px);
		width: 100%;
		position: relative;
		text-align: center;

		.wifi {
			height: 44px;
			width: 100%;
		}
		.password{
			height: 44px;
			width: 100%;
			border:  1px solid #F5F6F8;
		}
		.warn{
			margin: 10px 0;
			width: 100%;
			height: 50px;
			line-height: 50px;
			color: #FE8332;
			text-align: center;
			font-size: 15px;
			position: relative;
			img{
				position: absolute;
				top: 18px;
				left: 120px;
				height: 15px;
				width: 15px;
				display: block;
				
			}
		}
		p{
			position: absolute;
			height: 44px;
			width: 320px;
			top: 320px;
			left: 28px;
			color: rgba(56, 56, 56, 1);
			border: 1px solid rgba(204, 204, 204, 1);
			border-radius: 22px;
			font-size: 18px;
			line-height: 44px;
		}
	}
</style>
