<template>
	<div class="goods">
		<div class="title">
			<van-icon name="arrow-left" class="arrow" size="20px" @click="onClickLeft()" />
			<p>{{img[id].name}}</p>
			<van-icon name="ellipsis" class="more" size="20px" />
		</div>
		<div class="bg"><img :src="img[id].path" alt=""></div>
		<div class="des">
			<p>{{img[id].name}}</p>
			<p>{{img[id].ad}}</p>
			<p>{{img[id].des}}</p>
			<p><span class="new">￥{{img[id].price}}</span><span class="old">￥{{img[id].old}}</span></p>

		</div>
		
		<div class="info">
			<p class="choose-p"><span class="choose">已选：</span> {{img[id].title}} x{{img[id].num}}
				<van-icon name="arrow" class="arrow-right" size="20px" />
			</p>
		</div>
		<div class="desc">
			<p><img src="../assets/images/ok.svg" alt="">本产品为有品第三方商品</p>
			<p><img src="../assets/images/ok.svg" alt="">由北京石头世纪科技有限公司发货并提供售后</p>
			<p><img src="../assets/images/ok.svg" alt="">由北京石头世纪科技有限公司发货并提供售后</p>
			<van-icon name="arrow" class="arrow-right" size="20px" />
		</div>
		<div class="appraise">
			<p>商品评价（7362）</p>
			<p class="user"><img src="../assets/images/user.jpg" alt=""><span>r****2 </span></p>
			<p>第二次来买了，货比我想像中要好！！老板人表扬下。</p>
			<p>颜色分类：银色</p>
			<p>产看全部评价</p>
		</div>
		<div class="shop">
			<van-icon name="arrow-left" class="arrow" @click="moreback()" size="20px"/>
			<p>确认信息</p>
			<div class="shopinfo">
				<van-card
				  :num="img[id].num"
				  :price="img[id].price"
				  :desc="img[id].des"
				  :title="img[id].name"
				  :thumb="img[id].path"
				>
				  <template #footer>
				    <van-button size="large" class='btn' @click="reduce">-</van-button>
				    <van-button size="large" class='btn' @click="add">+</van-button>
				  </template>
				</van-card>
			</div>
			<div class="end"><p><span>合计:</span><span class="total">￥{{img[id].price*img[id].num}}元</span><span class="jiesuan" @click="goshop">加入购物车</span></p></div>
		<div class="login">
			<div class="tologin">
				<p @click="gologin">请立即登录！</p>
			</div>
		</div>
		</div>
		<div class="fixed">
			<van-goods-action>
				<van-goods-action-icon icon="chat-o" text="客服" />
				<van-goods-action-icon icon="cart-o" text="购物车" />
				<van-goods-action-icon icon="shop-o" text="店铺" />
				<van-goods-action-button type="danger" text="立即购买" @click="more"/>
			</van-goods-action>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {
		GoodsAction,
		GoodsActionIcon,
		GoodsActionButton,
		Card,
		Button,
		SubmitBar,
		Popup 
	} from 'vant';

	Vue.use(Popup);
	Vue.use(SubmitBar);
	Vue.use(Card);
	Vue.use(Button);
	Vue.use(GoodsAction);
	Vue.use(GoodsActionButton);
	Vue.use(GoodsActionIcon);
	import {
		Icon
	} from 'vant';

	Vue.use(Icon);
	export default {
		name: 'goods',
		data() {
			return {
				id: this.$route.query.Id,
				img: [{
					key:'1001',
					name: '扫地机器人',
					path: require('../assets/images/goods-bg.png'),
					ad: '【8.24-8.26开学季显示立省100元！到手价2399！】',
					des: '米家扫地机器人团队出品！传感器升级！长续航，智能规划，扫拖一体，清扫性更强！',
					price: 2399,
					old: 2499,
					title: '石头扫地机器人  银色',
					num: 1
				},
				{	key:'1002',
					name: '米家智能摄像头',
					path: require('../assets/images/monitor.jpg'),
					ad: '【8.24-8.26开学季显示立省100元！到手价2399！】',
					des: '米家扫地机器人团队出品！传感器升级！长续航，智能规划，扫拖一体，清扫性更强！',
					price: 2199,
					old: 2499,
					title: '米家智能摄像头  银色',
					num: 1
				}]
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			more() {
				var hmore = document.getElementsByClassName('shop')[0];
				hmore.style.width = "100%";
			},
			moreback() {
				var hmore = document.getElementsByClassName('shop')[0];
				hmore.style.width = 0;
			},
			add(){
				this.img[this.id].num+=1;
				if(this.img[this.id].num>=10){
					this.img[this.id].num=10;
				}
			},
			reduce(){
				this.img[this.id].num-=1;
				if(this.img[this.id].num<1){
					this.img[this.id].num=1;
				}
			},
			goshop(){
				var arr=[];
				arr = window.localStorage.getItem(this.img[this.id].key);
				if(arr){
					arr = arr.split(',');
					arr[2]=parseInt(arr[2])+parseInt(this.img[this.id].num);
					if(arr[2]>=10){
						arr[2]=10;
					}
					window.localStorage.setItem(this.img[this.id].key,arr);
				}
				else{
					arr=[]
					arr.push(this.img[this.id].name);
					arr.push(this.img[this.id].price);
					arr.push(this.img[this.id].num);
					arr.push(this.img[this.id].path);
					arr.push(this.img[this.id].key);
					window.localStorage.setItem(this.img[this.id].key,arr);
				}
				
				if(window.localStorage.getItem('key')){
					this.$router.push('/shop')
				}else{
					var login = document.getElementsByClassName('login')[0];
					login.style.display="block";
				}
				// console.log(	window.localStorage.getItem('address'))
				// arr = window.localStorage.getItem('address').split(',');
				// console.log(arr)
			}
			,
			gologin(){
				this.$router.push('/login')
			}
		}
	}
</script>

<style scoped lang="less">
	.shop{
		transition: all linear .5s;
		width: 0;
		height: 100%;
		position: fixed;
		z-index: 15;
		background: white;
		top: 0;
		right: 0;
		overflow: hidden;
		.login{
			height: 100%;
			width: 100%;
			position: absolute;
			top: 0;
			left: 0;
			display: none;
			z-index: 16;
			background: rgba(3,3,3,.5);
			.tologin{
				position: relative;
				height: 100px;
				width: 200px;
				line-height: 200px;
				background: white;
				text-align: center;
				margin: calc((100vh - 100px) / 2) auto;
				p{
					position: absolute;
					top: 0;
					left: 0;
