					top: 0;
				}
				.jiesuan{
					position: absolute;
					left: 275px;
					top: 0;
					height: 50px;
					width: 100px;
					line-height: 50px;
					color: rgba(255, 255, 255, 1);
						background-color: rgba(212, 48, 48, 1);
						font-size: 14px;
				}
			}
		}
		p{
			position: absolute;
			top: 15px;
			left: 170px;
			text-align: center;
			color: rgba(56, 56, 56, 1);
			font-size: 15px;
		}
		.shopinfo{
			padding-top: 50px;
			height: auto;
			width: 375px;
		}
		.arrow {
			position: absolute;
			top: 15px;
			left: 15px;
		}
	}
